import React, { Component } from "react"
import { Row, Col, Dropdown } from "react-bootstrap"
import { navigate } from "gatsby"
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
// import {
//   faChevronRight,
//   faChevronDown,
//   faChevronUp,
// } from "@fortawesome/free-solid-svg-icons"
// import { config } from "@fortawesome/fontawesome-svg-core"
import $ from "jquery"

import arrowRightWhite from "../../assets/svg/arrowRightWhite.svg"
import arrowRightGreen from "../../assets/svg/arrowRightGreen.svg"
import arrowUpGreen from "../../assets/svg/arrowUpGreen.svg"
import arrowDownGreen from "../../assets/svg/arrowDownGreen.svg"

import styles from "./SignUpForm.module.scss"
import classNames from "classnames"
import ExternalLink from "../Modal/ExternalLink"

// config.autoAddCss = false

export default class SignUpForm extends Component {
  constructor(props) {
    super(props)
    console.log('props --- ', props.register)
    this.state = {
      fullName: "",
      email: "",
      phoneNumber: "",
      zip: "",
      specialty: "Please Select Your Specialty",
      preferredContact: "No Preference",
      register: props.register || false,
      getInTouch: false,
      HCP: false,
      validName: false,
      validEmail: false,
      validPhone: false,
      validZip: false,
      checkedOne: false,
      dropdownIsOpen: false,
      hovered: 0,
    }
    this.formSubmitted = false
    this.emailRegex = new RegExp(
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    )
    this.phoneRegex = new RegExp(
      /[(]?\d{3}[)]?[- ]?\d{3}[- ]?\d{4}( x\d{4})?|x\d{4}/
    )
    this.zipRegex = new RegExp(/^\d{5,}$/)
    this.handleFormSubmit = this.handleFormSubmit.bind(this)
    this.selectOption = this.selectOption.bind(this)
    this.validateForm = this.validateForm.bind(this)
    this.formIsValid = this.formIsValid.bind(this)
    this.postData = this.postData.bind(this)
    this.getCookie = this.getCookie.bind(this)
    this.deleteCookies = this.deleteCookies.bind(this)
    this.mobileISIHandler = this.mobileISIHandler.bind(this)
  }

  componentDidMount() {
    if (
      typeof window !== "undefined" &&
      window.location.hash === "#getintouch"
    ) {
      this.setState({ getInTouch: true })
    }
  }

  handleHovered = num => {
    this.setState({
      hovered: num,
    })
  }

  update(field) {
    return e => {
      this.setState({
        [field]: e.currentTarget.value,
      })
      this.validateForm()
    }
  }

  toggleCheck(field) {
    switch (field) {
      case "register":
        this.setState({
          [field]: !this.state.register,
        })
        this.validateForm()
        break
      case "getInTouch":
        this.setState({
          [field]: !this.state.getInTouch,
        })
        this.validateForm()
        break
      case "HCP":
        this.setState({
          [field]: !this.state.HCP,
        })
        this.validateForm()
        break

      default:
        break
    }
  }

  validateForm() {
    this.state.fullName !== ""
      ? this.setState({ validName: true })
      : this.setState({ validName: false })
    this.emailRegex.test(this.state.email)
      ? this.setState({ validEmail: true })
      : this.setState({ validEmail: false })
    this.phoneRegex.test(this.state.phoneNumber)
      ? this.setState({ validPhone: true })
      : this.setState({ validPhone: false })
    this.zipRegex.test(this.state.zip)
      ? this.setState({ validZip: true })
      : this.setState({ validZip: false })
    this.state.register || this.state.getInTouch
      ? this.setState({ checkedOne: true })
      : this.setState({ checkedOne: false })
  }

  formIsValid() {
    if (
      this.state.fullName !== "" &&
      this.emailRegex.test(this.state.email) &&
      this.phoneRegex.test(this.state.phoneNumber) &&
      this.zipRegex.test(this.state.zip) &&
      (this.state.register || this.state.getInTouch) &&
      this.state.HCP
    ) {
      return true
    } else {
      return false
    }
  }

  handleFormSubmit(e) {
    e.preventDefault()
    this.formSubmitted = true
    this.validateForm()
    if (this.formIsValid()) {
      this.postData()
      this.deleteCookies()
      navigate("/thankyou/")
    }
  }

  selectOption(field, value) {
    this.setState({ [field]: value })
  }

  postData() {
    var data = {
      full_name: this.state.fullName,
      email: this.state.email,
      phone_number: this.state.phoneNumber,
      zip_code: this.state.zip,
      specialty: this.state.specialty,
      marketing_optin: this.state.register ? "yes" : "no",
      requested_contact: this.state.getInTouch ? "yes" : "no",
      preferred_contact_method: this.state.preferredContact,
      healthcare_professional_confirmation: this.state.HCP ? "yes" : "no",
      utm_source: this.getCookie("utm_source"),
      utm_medium: this.getCookie("utm_medium"),
      utm_campaign: this.getCookie("utm_campaign"),
      utm_content: this.getCookie("utm_content"),
      utm_term: this.getCookie("utm_term"),
    }

    $.ajax({
      type: "POST",
      // url: "https://aimt-app-crm-dev.azurewebsites.net/api/contact/hcp",
      url: "https://io.palforzia.com/api/contact/hcp",
      data: JSON.stringify(data),
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      error: e => console.log(e),
    })
  }

  getCookie(cname) {
    var name = cname + "="
    var decodedCookie = decodeURIComponent(document.cookie)
    var ca = decodedCookie.split(";")
    for (var i = 0; i < ca.length; i++) {
      var c = ca[i]
      while (c.charAt(0) == " ") {
        c = c.substring(1)
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length)
      }
    }
    return ""
  }

  mobileISIHandler(focus){
    if(focus === true){
        document.getElementById('isi-container').classList.add('hide-isi');
    } else {
      document.getElementById('isi-container').classList.remove('hide-isi');
    }
  }

  deleteCookies() {
    document.cookie = "utm_source="
    document.cookie = "utm_medium="
    document.cookie = "utm_campaign="
    document.cookie = "utm_content="
    document.cookie = "utm_term="
  }

  render() {
    console.log('props ', this.props)
    console.log('state ', this.state)
    return (
      <Row className={styles.formComponent}>
        <Col className={styles.form}>
          <form>
            <Row>
              <Col className={styles.inputField} lg={12} xs={12}>
                <input
                  type="text"
                  id="fullName"
                  required={true}
                  value={this.state.fullName}
                  onChange={this.update("fullName")}
                  onFocus={() => {this.mobileISIHandler(true)}}
                  onBlur={() => {this.mobileISIHandler(false)}}
                  className={
                    this.state.validName || !this.formSubmitted ? "" : styles.invalid
                  }
                />
                <label htmlFor="fullName">
                  <strong>Full Name*</strong>
                </label>
                <p
                  className={
                    this.state.validName || !this.formSubmitted
                      ? styles.hidden
                      : styles.invalid
                  }
                >
                  Please enter your full name.
                </p>
              </Col>
            </Row>

            <Row>
              <Col className="">
                <input
                  type="text"
                  id="eaddress"
                  required={true}
                  value={this.state.email}
                  onChange={this.update("email")}
                  onFocus={() => this.mobileISIHandler(true)}
                  onBlur={() => this.mobileISIHandler(false)}
                  className={
                    this.state.validEmail || !this.formSubmitted
                      ? ""
                      : styles.invalid
                  }
                />
                <label htmlFor="eaddress">
                  <strong>Email*</strong>
                </label>
                <p
                  className={
                    this.state.validEmail || !this.formSubmitted
                      ? styles.hidden
                      : styles.invalid
                  }
                >
                  Please enter your email address.
                </p>
              </Col>
            </Row>

            <Row>
              <Col lg={6} xs={12}>
                <input
                  type="text"
                  id="phoneNumber"
                  value={this.state.phoneNumber}
                  required={true}
                  onFocus={() => this.mobileISIHandler(true)}
                  onBlur={() => this.mobileISIHandler(false)}
                  onChange={this.update("phoneNumber")}
                  className={
                    this.state.validPhone || !this.formSubmitted
                      ? ""
                      : styles.invalid
                  }
                />
                <label htmlFor="phoneNumber">
                  <strong>Phone Number*</strong>
                </label>
                <p
                  className={
                    this.state.validPhone || !this.formSubmitted
                      ? styles.hidden
                      : styles.invalid
                  }
                >
                  Please enter your phone number.
                </p>
              </Col>
            </Row>

            <Row>
              <Col lg={6} xs={12}>
                <input
                  type="text"
                  id="zip"
                  value={this.state.zip}
                  required={true}
                  onChange={this.update("zip")}
                  onFocus={() => this.mobileISIHandler(true)}
                  onBlur={() => this.mobileISIHandler(false)}
                  className={
                    this.state.validZip || !this.formSubmitted ? "" : styles.invalid
                  }
                />
                <label htmlFor="zip">
                  <strong>Zip Code*</strong>
                </label>
                <p
                  className={
                    this.state.validZip || !this.formSubmitted
                      ? styles.hidden
                      : styles.invalid
                  }
                >
                  Please enter your ZIP code.
                </p>
              </Col>
            </Row>

            <Row>
              <Col lg={6} className={styles.formItem}>
                <Dropdown
                  className={styles.dropdown}
                  onToggle={() =>
                    this.setState({
                      dropdownIsOpen: !this.state.dropdownIsOpen,
                    })
                  }
                >
                  <Dropdown.Toggle
                    className={classNames(styles.toggleBtn, styles.textLeft, styles.btn)}
                    type="button"
                  >
                    {this.state.specialty}
                    <img
                      src={
                        this.state.dropdownIsOpen
                          ? arrowUpGreen
                          : arrowDownGreen
                      }
                      style={{ height: "18px" }}
                    ></img>
                  </Dropdown.Toggle>
                  <Dropdown.Menu className={styles.dropdownMenu}>
                    <Dropdown.Item
                      className={styles.dropdownItem}
                      onClick={() =>
                        this.selectOption("specialty", "Allergy/Immunology")
                      }
                    >
                      Allergy/Immunology
                    </Dropdown.Item>
                    <Dropdown.Item
                      className={styles.dropdownItem}
                      onClick={() =>
                        this.selectOption(
                          "specialty",
                          "Allergy/Immunology fellow"
                        )
                      }
                    >
                      Allergy/Immunology fellow
                    </Dropdown.Item>
                    <Dropdown.Item
                      className={styles.dropdownItem}
                      onClick={() =>
                        this.selectOption(
                          "specialty",
                          "Allied health professional"
                        )
                      }
                    >
                      Allied health professional
                    </Dropdown.Item>
                    <Dropdown.Item
                      className={styles.dropdownItem}
                      onClick={() =>
                        this.selectOption("specialty", "General pediatrics")
                      }
                    >
                      General pediatrics
                    </Dropdown.Item>
                    <Dropdown.Item
                      className={styles.dropdownItem}
                      onClick={() =>
                        this.selectOption("specialty", "Otolaryngology")
                      }
                    >
                      Otolaryngology
                    </Dropdown.Item>
                    <Dropdown.Item
                      className={styles.dropdownItem}
                      onClick={() =>
                        this.selectOption("specialty", "Nurse Practitioner")
                      }
                    >
                      Nurse Practitioner
                    </Dropdown.Item>
                    <Dropdown.Item
                      className={styles.dropdownItem}
                      onClick={() =>
                        this.selectOption("specialty", "Physician Assistant")
                      }
                    >
                      Physician Assistant
                    </Dropdown.Item>
                    <Dropdown.Item
                      className={styles.dropdownItem}
                      onClick={() =>
                        this.selectOption("specialty", "Other")
                      }
                    >
                      Other
                    </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
              </Col>
            </Row>

            <Row>
              <Col>
                <h3>I would like to*</h3>
                <p
                  className={
                    this.state.checkedOne || !this.formSubmitted
                      ? styles.hidden
                      : styles.invalid
                  }
                >
                  Please select at least one option.
                </p>
              </Col>
            </Row>
            <Row>
              <Col lg={6}>
                <Row
                  className={
                    this.state.checkedOne || !this.formSubmitted
                      ? styles.checkboxWrapper
                      : classNames(styles.checkboxWrapper, styles.invalid)
                  }
                >
                  <Col lg={1} xs={1}>
                    <label className={styles.checkbox}>
                      <input
                        type="checkbox"
                        value={this.state.register}
                        defaultChecked={this.state.register}
                      />
                      <span
                        className={classNames(
                          styles.checkmark,
                          {
                            [styles.checkmarkAfter]: this.state.register,
                          }
                        )}
                        onClick={() => this.toggleCheck("register")}
                        id="register-checkbox"
                      />
                    </label>
                  </Col>
                  <Col>Register for updates</Col>
                </Row>
              </Col>
            </Row>

            <Row>
              <Col>
                <p>
                  By registering for updates, I understand that I will be
                  receiving information about PALFORZIA by email, some of which
                  may be promotional in nature.
                </p>
              </Col>
            </Row>

            <Row>
              <Col>
                <div className={styles.sectionBorder} />
              </Col>
            </Row>

            <Row>
              <Col lg={6}>
                <Row
                  className={
                    this.state.checkedOne || !this.formSubmitted
                    ? styles.checkboxWrapper
                    : classNames(styles.checkboxWrapper, styles.invalid)
                  }
                >
                  <Col lg={1} xs={1}>
                    <label className={styles.checkbox} id="get-in-touch-label">
                      <input
                        type="checkbox"
                        value={this.state.getInTouch}
                        defaultChecked={
                          typeof window !== "undefined" &&
                          window.location.hash === "#getintouch"
                        }
                      />
                      <span
                        className={classNames(
                          styles.checkmark,
                          {
                            [styles.checkmarkAfter]: this.state.getInTouch,
                          }
                        )}
                        onClick={() => this.toggleCheck("getInTouch")}
                        id="get-in-touch-checkbox"
                      />
                    </label>
                  </Col>
                  <Col>Get in touch</Col>
                </Row>
              </Col>
            </Row>
            <Row>
              <Col>
                <p>
                  I understand that I will be contacted by an Aimmune Practice
                  Account Manager.
                </p>
              </Col>
            </Row>

            <Row>
              <Col lg={7} className={this.state.getInTouch ? "" : styles.hidden}>
                <Row>
                  <Col className={styles.preferredContactText}>
                    To get in touch, my preferred contact method is:
                  </Col>
                </Row>
                <Row>
                  <Col lg={4} xs={3} className={styles.radio}>
                    <input
                      id="phone"
                      type="radio"
                      name="who"
                      value="Phone"
                      onClick={this.update("preferredContact")}
                    />
                    <label htmlFor="phone" className={classNames(styles.textCenter, styles.firstLabel)}>
                      <strong>Phone</strong>
                    </label>
                  </Col>
                  <Col lg={4} xs={3} className={styles.radio}>
                    <input
                      id="email"
                      type="radio"
                      name="who"
                      value="Email"
                      onClick={this.update("preferredContact")}
                    />
                    <label htmlFor="email" className={classNames(styles.textCenter, styles.firstLabel)}>
                      <strong>Email</strong>
                    </label>
                  </Col>
                  <Col lg={4} xs={6} className={styles.radio}>
                    <input
                      id="nopreference"
                      type="radio"
                      name="who"
                      value="No Preference"
                      onClick={this.update("preferredContact")}
                      defaultChecked
                    />
                    <label
                      htmlFor="nopreference"
                      className={classNames(styles.textCenter, styles.firstLabel)}
                      
                    >
                      <strong>No Preference</strong>
                    </label>
                  </Col>
                </Row>
              </Col>
            </Row>

            <Row>
              <Col lg={7}>
                <Row
                  className={
                    this.state.HCP || !this.formSubmitted
                      ? classNames(styles.checkboxWrapper, styles.noBorder)
                      : classNames(styles.checkboxWrapper, styles.noBorder, styles.invalid)
                  }
                >
                  <Col lg={1} xs={1}>
                    <label className={styles.checkbox}>
                      <input
                        type="checkbox"
                        value={this.state.HCP}
                        required={true}
                      />
                      <span
                        className={classNames(
                          styles.checkmark,
                          {
                            [styles.checkmarkAfter]: this.state.HCP,
                          }
                        )}
                        onClick={() => this.toggleCheck("HCP")}
                        id="hcp-checkbox"
                      />
                    </label>
                  </Col>
                  <Col>I am a U.S Healthcare Professional*</Col>
                  <p
                    className={
                      this.state.HCP || !this.formSubmitted
                        ? styles.hidden
                        : styles.invalid
                    }
                  >
                    Please confirm if you are a US healthcare professional.
                  </p>
                </Row>
              </Col>
            </Row>

            <Row className={styles.terms}>
              <Col>
                <p>
                  By clicking “submit,” I authorize Aimmune and its affiliates
                  to use the personal data entered to provide me with updates
                  and information. I understand that receiving these
                  communications is optional, free, and that I have the ability
                  to opt out at any time.
                </p>
                <br />
                <p>
                  For more information, please see the Aimmune{" "}
                  <ExternalLink url="https://www.aimmune.com/privacy-notice">
                    Privacy Notice {" "}
                  </ExternalLink>
                  or contact{" "}
                  <a href="mailto:dataprivacy@aimmune.com">
                    dataprivacy@aimmune.com
                  </a>{" "}
                  if you have questions.
                </p>
              </Col>
            </Row>

            <Row>
              <Col lg={5} xs={12}>
                <button
                  className={styles.submitBtn}
                  onClick={this.handleFormSubmit}
                  onMouseEnter={() => this.handleHovered(1)}
                  onMouseLeave={() => this.handleHovered(0)}
                >
                  <span>Submit</span>
                  <img
                    src={
                      this.state.hovered === 1
                        ? arrowRightGreen
                        : arrowRightWhite
                    }
                  />
                  {/* <FontAwesomeIcon icon={faChevronRight} /> */}
                </button>
              </Col>
            </Row>
          </form>
        </Col>
      </Row>
    )
  }
}
