import React from "react"
import { Container, Row, Col } from "react-bootstrap"

import { graphql } from "gatsby"
import { useQueryParam, BooleanParam } from "use-query-params"

import { StateProvider } from "state/state"
import { reducer, initialState } from "state/reducer"

import Layout from "components/Layout"
import SEO from "components/SEO"
import SignUpForm from "components/SignUpForm/SignUpForm"
import PageTitle from "components/PageTitle/PageTitle"

import styles from "../styles/pages/signup.module.scss"

import "../styles/main.scss"

const GetInformed = ({ data }) => {
  const [register] = useQueryParam("register", BooleanParam)

  return (
    <StateProvider initialState={initialState} reducer={reducer}>
      <Layout>
        <SEO title={data.page.title} />
        <main className={styles.content}>
          <PageTitle title={data.page.title}></PageTitle>
          <Container className={styles.mobileOverride}>
            <Row className={styles.noPadding}>
              <Col className={styles.contactDetails}>
                <h2>Contact Details</h2>
                <p>*Required fields.</p>
              </Col>
            </Row>
          </Container>
          <Container className={styles.mobileOverride}>
            <SignUpForm register={register} />
          </Container>
        </main>
      </Layout>
    </StateProvider>
  )
}

export default GetInformed

export const query = graphql`
  query {
    page: contentfulHcpPage(label: { eq: "Get Informed" }) {
      label
      url
      title
    }
  }
`
